import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isTabbarShow: true,
    orderNavShow: true,
    table: {
      tableNum: '',
      ticketDetail: [], // ticket
      ticketPersonQuantity: '',
      ticketLength: 0,
      orderLength: 0,
      orderDetail: [], //   ticket
      changingOrderDetail: [] // ticket  patch 
    },
    tableId: '', // order $route   tableId
    categoryId: '1', // ordercategory  1
    dishes: [],
    selectingDish: [], //  create  data 
    selectedDish: {},
    ticketShow: false, //  (order.vue)  Ticket.vue 
    payShow: false, //  (pay.vue)  Pay.vue 
    updateCondition: false,
    payCondition: {}, // 
    wechatTicket: {},
    printList: []
  },
  mutations: {
    show (state) {
      state.isTabbarShow = true
    },
    hide (state) {
      state.isTabbarShow = false
    },
    changeTableNum (state, data) {
      // console.log(data.table_num, data.orderlength) //    order
      state.table.tableNum = data.table_num
      state.table.orderLength = data.orderlength
      state.table.ticketPersonQuantity = data.person_quantity
      // console.log(state.table.tableNum, state.table.orderLength, state.table.ticketPersonQuantity) //    order
    },
    changePersonQuantity (state, data) {
      console.log('vuex 41', state)
      console.log('vuex 42::::::', data)
    },
    changeOrderLength (state, orderlength) {
      state.table.orderLength = orderlength
    },
    changeTableId (state, id) {
      console.log(id)
      state.tableId = id
    },
    changeTicketPersonQuantity (state, parameter) {
      if (parameter === 'add') {
        state.table.ticketPersonQuantity += 1
      } else {
        state.table.ticketPersonQuantity -= 1
      }
    },
    changeCategoryId (state, id) {
      state.categoryId = id
    },
    changeTicketDetail (state, detail) {
      state.table.ticketDetail = detail
    },
    changeOrderDetail (state, orderdetail) {
      state.table.orderDetail = JSON.parse(JSON.stringify(orderdetail))
      console.log('orderDetail:::::::', state.table.orderDetail)
      // console.log('changingOrderDetail:::::::', state.table.changingOrderDetail)
    },
    // ticket.vue
    changeChangingOrderDetail (state, orderdetail) {
      state.table.changingOrderDetail = JSON.parse(JSON.stringify(orderdetail))
      state.table.changingOrderDetail = state.table.changingOrderDetail.map((item) => {
        Vue.set(item, 'checked', false)
        return item
      })
    },
    changeDishes (state, data) {
      state.dishes = data.map(function (item) {
        Vue.set(item, 'selecting', 0)
        return item
      })
    },
    resetDishes (state, data) {
      console.log('resetDish::::::', data)
      state.dishes = state.dishes.map(function (item) {
        Vue.set(item, 'selecting', 0)
        return item
      })
      state.table.ticketPersonQuantity = data[0].ticket_personquantity // 
    },
    changeSelectingDish (state, selectingList) {
      state.selectingDish = selectingList
    },
    changeSelectedDish (state, data) {
      state.selectedDish = data.map(function (item) {
        var Obj = {}
        Obj[item.dname] = { quantity: item.total_quantity[0], price: item.dprice, tax: item.dtax }
        // Obj[item.dname] = item.total_quantity[0]
        // Vue.set(item, 'price', item.dprice)
        // Obj.price = item.dprice
        // console.log(Obj)
        return Obj
      })
    },
    changeTicketShow (state) {
      state.ticketShow = !state.ticketShow
    },
    hideTicketShow (state) {
      console.log('ticket')
      state.ticketShow = false
    },
    falseUpdateCondition (state) {
      state.updateCondition = false
    },
    trueUpdateCondition (state) {
      state.updateCondition = true
    },
    changePayShow (state) {
      state.payShow = !state.payShow
    },
    hidePayShow (state) {
      console.log('pay')
      state.payShow = false
    },
    changePayCondition (state, data) {
      console.log('changePayCodition')
      console.log('', data[0])
      Vue.set(state.payCondition, 'virement', data[0].pay_by_transfer)
      Vue.set(state.payCondition, 'cash', data[0].pay_by_cash)
      Vue.set(state.payCondition, 'ticket', data[0].pay_by_ticket)
      Vue.set(state.payCondition, 'card', data[0].pay_by_card)
      Vue.set(state.payCondition, 'check', data[0].pay_by_check)
      Vue.set(state.payCondition, 'wechat', data[0].pay_by_wechat)
      Vue.set(state.payCondition, 'alipay', data[0].pay_by_alipay)
    },
    changeOrderNavShow (state) {
      state.orderNavShow = !state.orderNavShow
    },
    changeWechatTicket (state, data) {
      console.log(data)
      state.wechatTicket = data
    },
    changePrintList (state, data) {
      // console.log(data)
      // state.printList.push(data)
      state.printList = data
      Vue.set(state.printList, 0, data)
      console.log('printList', state.printList)
    }
  },
  actions: {
  },
  modules: {
  }
})
