<template>
  <div class="nav">
    <div class="nva_bottom" id="confirm" @click="handleSubmit">
      <p></p>
    </div>
    <div class="nav_icon">
      <i class="iconfont icon-gouwuche"
     style="margin-left: 15px; border: border-radius: 50%; font-size:2em; color: rgba(7, 17, 27, 0.5); font-weight: normal"
     @click="handleTicketClick"></i>
      <i class="iconfont icon-jiezhang"
     style="margin-left: 15px; border: 2px solid white; border-radius: 50%; font-size:2em; color:rgba(7, 17, 27, 0.5); font-weight: bolder"
     @click="handlePayClick"></i>
    </div>
  </div>
</template>
<script>
import Vue from 'vue'
import axios from 'axios'

// updatePersonQuantity: 
function updatePersonQuantity (that) {
  if (that.$store.state.table.ticketPersonQuantity > 1) {
    console.log('Navbar 19')
    axios({
      url: `/testpatchtable/${that.$store.state.tableId}/`,
      method: 'PATCH',
      data: { person_quantity: that.$store.state.table.ticketPersonQuantity }
    }).then(res => {
      console.log('', res.data)
    })
  } else {
    console.log(' Navbar 21')
  }
}

// createOrderorder
function createOrder (that) {
  // console.log('that::::::::::::', that)
  that.$store.state.selectingDish.forEach((item) => {
    console.log(item.selecting)
    if (item.selecting > 0) {
      var date = new Date()
      var orderTime = date.getFullYear().toString() +
                      (((date.getMonth() + 1).toString().length < 2) ? '0' + (date.getMonth() + 1).toString() : (date.getMonth() + 1).toString()) +
                      date.getDate().toString() +
                      (((date.getHours()).toString().length < 2) ? '0' + date.getHours().toString() : date.getHours().toString()) +
                      (((date.getMinutes()).toString().length < 2) ? '0' + date.getMinutes().toString() : date.getMinutes().toString()) +
                      (((date.getSeconds()).toString().length < 2) ? '0' + date.getSeconds().toString() : date.getSeconds().toString())
      var orderData = {}
      Vue.set(orderData, 'order_id', 'D' + date.getTime().toString() + Math.floor(Math.random() * (999 - 100 + 1) + 100).toString())
      Vue.set(orderData, 'order_time', orderTime)
      Vue.set(orderData, 'quantity', item.selecting)
      Vue.set(orderData, 'code', item.dcode)
      Vue.set(orderData, 'tid', that.$store.state.tableId)
      Vue.set(orderData, 'o_note', '')
      console.log(JSON.stringify(orderData))
      console.log(orderData)
      axios({
        url: '/testcreate/',
        method: 'post',
        data: orderData
      }).then(res => {
        console.log('tableNum:', that.$route.params.table_num)
        axios({
          url: `/testticket/${that.$route.params.table_num}/`
        }).then(res => {
          // console.log('order', res.data.length)
          // that.$store.commit('changeOrderLength', res.data.length)
          // console.log(':::::::::::::::', res.date)
          that.$store.commit('changePersonQuantity', res.data)
          // that.$store.commit('changeTableNum', { table_num: this.$route.params.table_num, orderlength: res.data.length, person_quantity: parseInt(res.data[0].ticket_personquantity) })
          that.$store.commit('resetDishes', res.data)
          axios({
            url: `/testticketordergroupby/${that.$route.params.table_num}/`
          }).then(res => {
            console.log(res.data)
            // res.data    
            // res.data[0].tableid[0]  tableId
            // console.log(res.data[0].tableid[0])
            that.$store.commit('changeTicketDetail', res.data)
            that.$store.commit('changeSelectedDish', res.data)
          })
        })
      })
    }
  })
}

function createTicket (that) {
  console.log('ticket')
  var date = new Date()
  var ticketData = {}
  ticketData.ticket_id = 'T' + date.getTime().toString()
  ticketData.ticket_time = date.getFullYear().toString() +
              (((date.getMonth() + 1).toString().length < 2) ? '0' + (date.getMonth() + 1).toString() : (date.getMonth() + 1).toString()) +
              date.getDate().toString() +
              (((date.getHours()).toString().length < 2) ? '0' + date.getHours().toString() : date.getHours().toString()) +
              (((date.getMinutes()).toString().length < 2) ? '0' + date.getMinutes().toString() : date.getMinutes().toString()) +
              (((date.getSeconds()).toString().length < 2) ? '0' + date.getSeconds().toString() : date.getSeconds().toString())
  // ticketData.table_num = that._route.params.table_num
  if (that.$store.state.table.tableNum) {
    ticketData.table_num = that.$store.state.table.tableNum
  } else {
    ticketData.table_num = that.$route.params.table_num
  }
  // console.log('::::::', that.$store.state.table.tableNum)
  // console.log('::::::', that.$route.params.table_num)
  const _this = that
  console.log(ticketData)
  // console.log('_this:::::::::::::::', _this)
  axios({
    url: '/testcreatetable/',
    method: 'post',
    data: ticketData
  }).then((res) => {
    // console.log('ticket', res.data)
    _this.$store.state.tableId = res.data.ticket_id
    // axios({
    //   url: '/testticket/' + _this.$route.params.table_num + '/'
    // }).then(res => {
    //   console.log(':::::::::::::::', res.data)
    //   // _this.$store.commit('changeTableNum', { table_num: _this.$route.params.table_num, orderlength: res.data.length, person_quantity: parseInt(res.data[0].ticket_personquantity) })
    //   console.log('axios', _this.$store.state.table.ticketPersonQuantity)
    // }).catch((err) => {
    //   console.log('axios', err)
    // })
    updatePersonQuantity(_this)
    createOrder(_this)
  }).catch((err) => {
    console.log('ticket', err)
  })
}

// tableId2
function getTableId (that) {
  axios({
    url: `/testemptyticket/${that.$route.params.table_num}/`
  }).then(res => {
    return res.data[0].ticket_id
  }).catch(err => {
    console.log('2tableId', err)
  })
}

export default {
  methods: {
    handleSubmit () {
      console.log('')
      // console.log('ticketDetail.length::::::::::', this.$store.state.table.ticketDetail.length)
      if (this.$store.state.table.ticketDetail.length > 0) {
        console.log('ticketorder')
        updatePersonQuantity(this)
        // order
        createOrder(this)
      } else {
        console.log('ticketDetail0ticket')
        axios({
          url: `/testemptyticket/${this.$route.params.table_num}/`
        }).then(res => {
          // console.log('res.data[0].ticket_id', res.data[0].ticket_id)
          console.log('(2):::::::::::::::::', this.$store.state.table.ticketPersonQuantity)
          updatePersonQuantity(this)
          if (this.$store.state.table.ticketPersonQuantity) {
            console.log('patch personQuantity ')
          }
          if (res.data[0].ticket_id) {
            console.log('ticket(2)state.tableId')
            this.$store.commit('changeTableId', res.data[0].ticket_id)
            createOrder(this)
          } else {
            console.log('')
            createTicket(this)
          }
        }).catch(err => {
          console.log('catch', err)
          createTicket(this)
        })
        // console.log('ticket')
        // createTicket(this)
      }
    },
    handleTicketClick () {
      this.$store.commit('changeTicketShow')
      axios({
        url: '/testticket/' + this.$route.params.table_num + '/'
      }).then(res => {
        this.$store.commit('changeOrderDetail', res.data)
        this.$store.commit('changeChangingOrderDetail', res.data)
        // console.log('order', this.$store.state.table.orderDetail)
        // console.log(this.$store.state.table.orderLength)
      })
    },
    handlePayClick () {
      this.$store.commit('changePayShow')
      // this.$store.commit('changeOrderNavShow')
      axios({
        url: `/testticketordergroupby/${this.$route.params.table_num}/`
      }).then(res => {
        console.log('groupBy', res.data)
        // res.data    
        // res.data[0].tableid[0]  tableId
        // console.log(res.data[0].tableid[0])
        this.$store.commit('changeTicketDetail', res.data)
        // console.log(this.$store.state.table.ticketDetail)
        // console.log(this.$store.state.table.ticketLength)
        //     0 
        if (res.data.length > 0) {
          this.$store.commit('changeTableId', res.data[0].tableid[0])
        } else if (getTableId(this)) {
          this.$store.commit('changeTableId', getTableId(this))
        }
        // this.selectedDish = res.data.map(item => {item.dname: item.quantity})
        this.$store.commit('changeSelectedDish', res.data)
        // console.log('', res.data)
      })
      async function getPay (that) {
        const response = await axios({
          url: `/testticketdetail/${that.$route.params.table_num}/`
        })
        console.log(response.data)
        that.$store.commit('changePayCondition', response.data)
      }
      getPay(this)
    }
  }
}
</script>
<style lang="scss" scoped>
  .nav {
    border-top: 1px solid rgba(7, 17, 27, 0.1);
    position: fixed;
    bottom: 0px;
    /*background-color: #B2DFDB;*/
    // background-color: rgba(7, 17, 27, 0.1);
    width: 100%;
    height: 70px;
    z-index: 9;
    // box-shadow: 0px -1px #eee;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
    #confirm{
      width: 20%;
      height: 100%;
      background-color: orangered;
      color: white;
      font-weight: bolder;
      text-align: center;
      justify-content: center;
      align-items: center;
      display: flex;
    }
    .nav_icon {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: center;
    }
  }
  .up {
    animation: toggleup 0.2s;
    transform: rotate(0deg)
  }
  .down {
    animation: toggledown 0.2s;
    transform: rotate(180deg);
  }
  @keyframes toggledown {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(180deg);
    }
  }
  @keyframes toggleup {
    0% {
      transform: rotate(180deg);
    }
    100% {
      transform: rotate(0deg);
    }
  }
</style>
