<template>
  <div>
    <v-btn @click="startListen"></v-btn>
    <v-btn @click="stop = true"></v-btn>
    <v-btn @click="getData"></v-btn>
    <v-btn @click="test"></v-btn>
  </div>
</template>

<script>
import axios from 'axios'
import { getLodop } from '../utils/LodopFuncs' // 
export default {
  data () {
    return {
      inteval: {},
      stop: false
    }
  },

  computed: {
    printContent () {
      console.log('computed')
      return this.$store.state.printList
    }
  },

  methods: {
    async test () {
      const res = await axios.get('/testprintlist/')
      const ticket = JSON.parse(res.data[0].print_content)
      const LODOP = getLodop()
      const strHtml = `
      <div style="width: 100%">
        <ul style="width: 90%; list-style:none">
          ${ticket.orders.map((item) =>
          `<li style="list-style:none; margin-left:-30px; width: 100%;
          min-height: 40px; border: 1px dashed black; font-weight: 800;
          display: flex; align-items: center">
            <p style="flex: 1; align-self: center">${item.quantity}</p>
            <p style="flex: 3">${item.dname}</p>
            <p style="flex: 3">${item.note}</p>
          </li>`).join('')}
        </ul>
      </div>
      `
      LODOP.PRINT_INIT('')
      LODOP.ADD_PRINT_HTML(0, 0, '100%', '100%', strHtml)
      LODOP.SET_PRINT_PAGESIZE(3, 800, 50, '')
      // LODOP.PREVIEW()
      // LODOP.PRINT_DESIGN()
      LODOP.PRINT()
    },

    async getData () {
      const res = await axios.get('/testprintlist/')
      console.log(res.data)
    },

    // 
    printCuisine (ticket) {
      const LODOP = getLodop()// getLodopLODOP
      // console.log(JSON.parse(ticket.print_content).map(item => item))
      console.log(JSON.parse(ticket.print_content).orders)
      JSON.parse(ticket.print_content).orders.map(item => console.log(item.note.length))
      const strHtml = `
      <div style="width: 100%; margin-bottom: 20px">
        <ul style="width: 90%; list-style:none;">
          ${JSON.parse(ticket.print_content).orders.map(item =>
          `<li style="list-style:none; margin-left:-30px; width: 100%;
          height: ${item.note === '' && item.dname.length <= 11 ? '30px' : '45px'}; border: 1px dashed black; font-weight: 800;
          display: flex; align-items: center">
            <p style="flex: 0 0 30px; text-align: center;">${item.quantity}</p>
            <p style="flex: 5">${item.dname}</p>
            <p style="flex: 3; display: ${item.note === '' ? 'none' : 'block'};">${item.note}</p>
          </li>`).join('')}
        </ul>
      </div>
      <div style="min-height: 10px"></div>
      `
      LODOP.PRINT_INIT('')
      LODOP.ADD_PRINT_HTM(20, 0, '100%', '100%', strHtml)
      /*
      // LODOP.SET_PRINT_PAGESIZE: 
      //    - 0134
      //    - 
      //    - :
      //    - 
      */
      LODOP.SET_PRINT_PAGESIZE(3, 800, 50, '')
      // LODOP.PREVIEW()
      LODOP.PRINT()
    },

    // 
    printSalle (ticket) {
      const LODOP = getLodop()// getLodopLODOP
      // console.log(JSON.parse(ticket.print_content).map(item => item))
      console.log(JSON.parse(ticket.print_content).orders)
      const strHtml = `
      <div style="width: 100%;">
        <div style="width: 90%; margin-left: 10px; font-weight: 800; border-bottom: 1px dotted black;">
          <span style="display: flex">
            <p style="display:${JSON.parse(ticket.print_content).client_name === null ? 'none' : 'block'};">${JSON.parse(ticket.print_content).client_name}</p>
            <p style="margin-left: 10px;display:${JSON.parse(ticket.print_content).client_telephone === null ? 'none' : 'block'};">${JSON.parse(ticket.print_content).client_telephone}</p>
          </span>
          <p style="margin-top:0px;display: ${JSON.parse(ticket.print_content).delivery_time === null ? 'none' : 'block'};">${JSON.parse(ticket.print_content).delivery_time}</p>
          <p style="margin-top:0px;display:${JSON.parse(ticket.print_content).client_address === null ? 'none' : 'block'};">${JSON.parse(ticket.print_content).client_address}</p>
          <p style="margin-top:0px;display:${JSON.parse(ticket.print_content).ticket_note === null ? 'none' : 'block'};">${JSON.parse(ticket.print_content).ticket_note}</p>
        </div>
        <ul style="width: 90%; list-style:none">
          <li  style="list-style:none; margin-left:-30px; margin-top: -10px;
          width: 100%; min-height: 5px; font-weight: 500;
          align-items: center">
            <span style="display: flex">
              <p style="flex: 1; align-self: center"></p>
              <p style="flex: 4; align-self: center;"></p>
              <p style="flex: 1; align-self: center"></p>
            </span>
          </li>
          ${JSON.parse(ticket.print_content).orders.map(item =>
          `<li style="list-style:none; margin-left:-30px; margin-top: -20px;
          width: 100%; min-height: 5px; font-weight: 400;
          align-items: center">
            <span style="display: flex">
              <p style="flex: 1; align-self: center">${item.quantity}</p>
              <p style="flex: 4; align-self: center;">${item.dname}</p>
              <p style="flex: 1; align-self: center">${(parseFloat(item.quantity) * parseFloat(item.dprice)).toFixed(2)}</p>
            </span>
            <p style="display: ${item.note === '' ? 'none' : 'block'};flex: 3; margin-top:-12px;
            margin-left: 42px; width: 80%; text-decoration:underline;">${item.note}</p>
          </li>`).join('')}
        </ul>
        <div>
          <p style="width: 90%; margin-left: 10px; text-align: right; font-weight: 800;">: ${JSON.parse(ticket.print_content).ticket_price}</p>
          <p style="margin-top:-10px;width: 90%; margin-left: 10px; text-align: right; font-size:12px; height: 20px">${(JSON.parse(ticket.print_content).ticket_price * 7.95).toFixed(0)}</p>
        </div>
      </div>
      <div style="min-height: 10px"></div>
      `
      LODOP.PRINT_INIT('')
      LODOP.ADD_PRINT_HTM(30, 0, '100%', '100%', strHtml)
      LODOP.SET_PRINT_PAGESIZE(3, 800, 50, '')
      // LODOP.PREVIEW()
      LODOP.PRINT()
      // LODOP.PRINT_DESIGN()
    },

    printGroupby (dishes) {
      const LODOP = getLodop()// getLodopLODOP
      // console.log(JSON.parse(ticket.print_content).map(item => item))
      console.log(dishes)
      console.log(JSON.parse(dishes.print_content))
      const strHtml = `
      <div style="width: 100%">
        <ul style="width: 90%; list-style:none">
          ${JSON.parse(dishes.print_content).map(item =>
          `<li style="list-style:none; margin-left:-30px; width: 100%;
          border: 1px dashed black; font-weight: 800;
          height: ${item.note_content ? 'auto' : '30px'};
          display: flex; align-items: center">
            <p style="flex: 1; align-self: center; text-align: center; font-size:12px;
            ">${item.emergency === 0 ? '' : ''}</p>
            <p style="flex: 1; align-self: center; text-align: center; font-size:18px;">${item.total_quantity[0]}</p>
            <p style="flex: 7">${item.dname}</p>
            <p style="flex: 5; display: ${item.note_content ? 'block' : 'none'};
            white-space: pre-wrap; text-align: left"">${item.note_content}</p>
          </li>`).join('')}
        </ul>
      </div>
      `
      LODOP.PRINT_INIT('')
      LODOP.ADD_PRINT_HTM(30, 0, '100%', '100%', strHtml)
      /*
      // LODOP.SET_PRINT_PAGESIZE: 
      //    - 0134
      //    - 
      //    - :
      //    - 
      */
      LODOP.SET_PRINT_PAGESIZE(3, 800, 50, '')
      LODOP.PREVIEW()
      // LODOP.PRINT()
    },

    async printMain () {
      const res = await axios.get('/testprintlist/')
      console.log(res.data)
      if (res.data.length > 0) {
        // print_type0cuisine
        if (res.data[0].print_type === 0) {
          this.printCuisine(res.data[0])
          axios({
            url: `/testprintlist/${res.data[0].print_id}/`,
            method: 'DELETE'
          })
        } else if (res.data[0].print_type === 1) { // print_type1salle
          this.printSalle(res.data[0])
          axios({
            url: `/testprintlist/${res.data[0].print_id}/`,
            method: 'DELETE'
          })
        } else if (res.data[0].print_type === 2) { // print_type2groupBy
          this.printGroupby(res.data[0])
          axios({
            url: `/testprintlist/${res.data[0].print_id}/`,
            method: 'DELETE'
          })
        }
      } else {
        console.log('')
      }
    },

    async startListen () {
      this.stop = false
      // const res = await axios.get('/testprintlist/')
      const inteval = setInterval(() => {
        if (this.stop === true) {
          clearInterval(inteval)
        }
        this.printMain()
      }, 1000)
    }
  }
}
</script>
